	var _self,
		page = 1,
		timer = null;
export default {
	data() {
		return {
			baseUrl:"",
			coms:[],
			loadingText: '加载中...',
			payChoose:false,
			hide:false,
			alipy: true,
			wxpay: false,
			shopNum:"",
			userToken:false
		};
	},
	filters: {
	       formatDate: function (value) {
	        var date = new Date(value);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
	        var Y = date.getFullYear() + '-';
	        var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
	        var D = date.getDate() + ' ';
	        var h = date.getHours() + ':';
	        var m = date.getMinutes() + ':';
	        var s = date.getSeconds();
	        return Y+M+D+h+m+s;
	      }
	    },
		computed: {
			i18n() {
				return this.$t('index');
			}
		},
	onLoad: function() {
		_self = this;
		console.log('1');
		this.getnewsList();
		let token = uni.getStorageSync('storage_token');
		if(token == "" || token == null || token == undefined){
			this.userToken = false
		}else {
			this.userToken = true;
		}
		
		uni.setNavigationBarTitle({
			title: this.$t('index.orders')
		})	
	},
	// 下拉
	onPullDownRefresh: function() {
		console.log('2');
		this.getnewsList();
	},
	// 上拉
	onReachBottom: function() {
		console.log('3');
		if (timer != null) {
			console.log('4');
			clearTimeout(timer);
		}
		timer = setTimeout(function() {
			_self.getmorenews();
		}, 1000);
	},
	methods: {
		go_login(){
			uni.navigateTo({
				url:"../login/login"
			})
		},
		// 上拉下拉时调用
		getmorenews: function() {
			this.baseUrl = this.websiteUrl;
			let token = uni.getStorageSync('storage_token');
			if (_self.loadingText != '' && _self.loadingText != '加载更多') {
				return false;
			}
			_self.loadingText = '加载中...';
			uni.showNavigationBarLoading();
			uni.request({
				url: this.baseUrl + '/api/myorders?count=10&page=' + page,
				method: 'GET',
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					token: token
				},
				success: function(res) {
					_self.loadingText = '';
					if (res.data.data == null) {
						uni.hideNavigationBarLoading();
						_self.loadingText = '已加载全部';
						return false;
					}
					page++;
					console.log(res);
				_self.coms = _self.coms.concat(res.data.data);
					
					_self.loadingText = '加载更多';
					uni.hideNavigationBarLoading();
				}
			});
		},
		// 初始化时调用/
		getnewsList: function() {
			this.baseUrl = this.websiteUrl;
			let token = uni.getStorageSync('storage_token');
			page = 1;
			uni.showNavigationBarLoading();
			uni.request({
				url: this.baseUrl + '/api/myorders?count=10&page=' + page,
				method: 'GET',
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					token: token
				},
				success: function(res) {
					page++;
					_self.coms = res.data.data;
					uni.hideNavigationBarLoading();
					uni.stopPullDownRefresh();
					_self.loadingText = '加载更多';
				}
			});
		},
		gerenClick() {
			this.wxpay = true;
			this.alipy = false;
		},
		qiyeClick() {
			this.wxpay = false;
			this.alipy = true;
		},
		buyNum(id) {
			this.payChoose = true;
			this.hide = true;
			this.shopNum = id;
			console.log(this.shopNum);
		},
		sure() {
			// this.flag = false;
			var alipy;
			if (this.alipy == true) {
				alipy = '2';
			} else {
				alipy = '1';
			}
			console.log(alipy);
			this.baseUrl = this.websiteUrl;
			let token = uni.getStorageSync('storage_token');
			// pay_type  1/微信 2/支付宝  3/小程序
			// type 1/VIP 2/超出后购买 3/自动续费 4/会员免费单词查询
			uni.request({
				url: this.baseUrl + '/api/payOrder',
				method: 'GET',
				data: {
					pay_type: alipy,
					order_no: this.shopNum
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					token: token
				},
				success: res => {
					if (res.data.code == 200) {
						console.log(res.data);
						// console.log(res.data.data.payData);
						// orderInfo 微信/支付宝订单数据
						// provider:provider  wxpay/微信 alipay支付宝
		let orderInfo = res.data.data.payData;
			var paysss;
			console.log(orderInfo);
			if(alipy == 1){
				paysss = "wxpay";
				uni.requestPayment({
					provider: paysss,
					orderInfo: JSON.stringify(orderInfo),
					success: function(res) {
						console.log('success:' + JSON.stringify(res));
					},
					fail: function(err) {
						console.log('fail:' + JSON.stringify(err));
					}
				});
			}else if(alipy == 2){
				let orderInfo = res.data.data.payData.payData;
				paysss = "alipay";
				uni.requestPayment({
					provider: paysss,
					orderInfo: orderInfo,
					success: function(res) {
						console.log('success:' + JSON.stringify(res));
					},
					fail: function(err) {
						console.log('fail:' + JSON.stringify(err));
					}
				});
			}
			console.log(paysss);
			// uni.requestPayment({
			// 	provider: paysss,
			// 	orderInfo: JSON.stringify(orderInfo),
			// 	success: function(res) {
			// 		console.log('success:' + JSON.stringify(res));
			// 	},
			// 	fail: function(err) {
			// 		console.log('fail:' + JSON.stringify(err));
			// 	}
			// });
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		cancel() {
			this.hide = false;
			this.payChoose = false;
		},
		// country() {
			// uni.navigateTo({
			// 	url: '../orderdetail/orderdetail'
			// });
		// },
		// onNavigationBarButtonTap() {
		// 	console.log('你点击了按钮');
		// },
	}
};
